#feedback .feedback-button button {
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
  display: block;
  position: relative;
  width: 300px;
  height: 78px;
  transition: transform 0.3s ease-in;
  border-radius: 40px;
}
@media (max-width: 992px) {
  #feedback .feedback-button button {
    width: 230px;
    height: 65px;
  }
}
@media (max-width: 576px) {
  #feedback .feedback-button button {
    width: 150px;
    height: 40px;
  }
}
#feedback .feedback-button button .button-background {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: block;
  background-color: #468217;
  transform-origin: center;
  transition: all 0.2s ease;
  box-shadow: 0px 6px 24px rgba(70, 130, 23, 0);
  z-index: 2;
  border-radius: 40px;
  overflow: hidden;
}
#feedback .feedback-button button .button-background.button-animation::before {
  content: "";
  opacity: 0.7;
  position: absolute;
  top: 0;
  transform: translateX(-125%);
  width: 100%;
  height: 100%;
  background: linear-gradient(100deg, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0) 25%, hsla(0, 0%, 84%, 0.25) 51%, rgba(0, 0, 0, 0) 76%, rgba(0, 0, 0, 0));
  filter: blur(2px);
  transition: opacity 1s ease;
  animation: sheen 3s forwards infinite;
}
#feedback .feedback-button button .button-text {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 22px;
  line-height: 100%;
  color: #ffffff;
  z-index: 3;
  position: relative;
}
@media (max-width: 992px) {
  #feedback .feedback-button button .button-text {
    font-size: 18px;
  }
}
@media (max-width: 576px) {
  #feedback .feedback-button button .button-text {
    font-weight: 400;
    font-size: 14px;
    line-height: 100%;
  }
}
#feedback .feedback-button button:not(:disabled):hover .button-background, #feedback .feedback-button button:not(:disabled):focus .button-background {
  transition: all 0.2s ease-in 0.1s;
  background-color: #417915;
  box-shadow: 0px 6px 24px rgba(70, 130, 23, 0.39);
}
#feedback .feedback-button button:not(:disabled):hover .button-background::before, #feedback .feedback-button button:not(:disabled):focus .button-background::before {
  opacity: 0;
}
#feedback .feedback-button button:not(:disabled):hover .button-background {
  transform: scale(0.96);
}
#feedback .feedback-button button:not(:disabled):active {
  transform: translateY(3px);
}
@keyframes sheen {
  100% {
    transform: translateX(100%);
  }
}
#feedback .feedback-button button:disabled .button-background {
  background-color: rgba(95, 95, 95, 0.8784313725);
}

#feedback .feedback-item {
  background: #ffffff;
  border: 3px solid #ececec;
  box-shadow: 15px 15px 40px rgba(0, 0, 0, 0.1);
  border-radius: 20px;
}

#feedback {
  padding-bottom: 100px;
}
@media (max-width: 992px) {
  #feedback {
    padding-bottom: 80px;
  }
}
@media (max-width: 768px) {
  #feedback {
    padding-bottom: 60px;
  }
}

#feedback {
  overflow: hidden;
}
#feedback .feedback-wrapper {
  padding: 0 10px;
}
@media (min-width: 769px) {
  #feedback .feedback-wrapper {
    padding: 0 20px;
    margin: 10px 50px;
  }
}
#feedback .feedback-wrapper__container {
  position: relative;
}
#feedback .feedback-wrapper .swiper-wrapper {
  padding-bottom: 50px;
}
@media (max-width: 992px) {
  #feedback .feedback-wrapper .swiper-wrapper {
    padding-bottom: 40px;
  }
}
#feedback .feedback-wrapper .swiper-wrapper .swiper-slide {
  opacity: 0;
  transition: opacity 0.1s 0s;
}
#feedback .feedback-wrapper .swiper-wrapper .swiper-slide-active {
  opacity: 1;
}
@media (min-width: 993px) {
  #feedback .feedback-wrapper .swiper-wrapper .swiper-slide-next + .swiper-slide {
    opacity: 1;
  }
}
@media (min-width: 769px) {
  #feedback .feedback-wrapper .swiper-wrapper .swiper-slide-next {
    opacity: 1;
  }
}
@media (max-width: 768px) {
  #feedback .feedback-wrapper .feedback-navigation {
    position: absolute;
    display: flex;
    justify-content: space-between;
    top: auto;
    bottom: 20px;
    transform: translateY(-60px) translateX(-50%);
    left: 50%;
    width: 130px;
  }
}
#feedback .feedback-wrapper .feedback-navigation .feedback-btn__prev,
#feedback .feedback-wrapper .feedback-navigation .feedback-btn__next {
  position: absolute;
  z-index: 5;
  top: 50%;
  transform: translateY(calc(-50% - 60px));
}
@media (max-width: 768px) {
  #feedback .feedback-wrapper .feedback-navigation .feedback-btn__prev,
  #feedback .feedback-wrapper .feedback-navigation .feedback-btn__next {
    position: static;
    transform: translateY(0);
    left: auto;
    right: auto;
  }
}
#feedback .feedback-wrapper .feedback-navigation .feedback-btn__prev {
  left: 0px;
}
#feedback .feedback-wrapper .feedback-navigation .feedback-btn__next {
  right: 0px;
}
#feedback .feedback-item {
  overflow: hidden;
  height: auto;
  display: flex;
  flex-direction: column;
  gap: 30px;
}
@media (max-width: 992px) {
  #feedback .feedback-item {
    gap: 20px;
  }
}
@media (max-width: 768px) {
  #feedback .feedback-item {
    max-width: 300px;
    margin: 0 auto;
  }
}
#feedback .feedback-item__line {
  padding: 0 30px 50px;
  padding-bottom: 0 !important;
}
@media (max-width: 1280px) {
  #feedback .feedback-item__line {
    padding: 0 30px 40px;
  }
}
@media (max-width: 1024px) {
  #feedback .feedback-item__line {
    padding: 0 20px 30px;
  }
}
@media (max-width: 768px) {
  #feedback .feedback-item__line {
    padding-bottom: 95px;
  }
}
#feedback .feedback-item__btn {
  padding: 0 30px 50px;
  display: flex;
  justify-content: center;
}
@media (max-width: 1280px) {
  #feedback .feedback-item__btn {
    padding: 0 30px 40px;
  }
}
@media (max-width: 1024px) {
  #feedback .feedback-item__btn {
    padding: 0 20px 30px;
  }
}
@media (max-width: 768px) {
  #feedback .feedback-item__btn {
    padding-bottom: 95px;
  }
}
#feedback .feedback-item__btn .link-flash {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 22px;
  line-height: 115%;
  white-space: nowrap;
  text-align: center;
  font-size: calc(0.005 * 100vw + 12.4px);
  cursor: pointer;
  color: #2b2b2b;
  position: relative;
  display: inline;
  padding: 5px 0;
}
@media (max-width: 768px) {
  #feedback .feedback-item__btn .link-flash {
    font-size: 16px;
  }
}
@media (min-width: 1921px) {
  #feedback .feedback-item__btn .link-flash {
    font-size: 22px;
  }
}
@media (max-width: 320px) {
  #feedback .feedback-item__btn .link-flash {
    font-size: 14px;
  }
}
#feedback .feedback-item__btn .link-flash::before, #feedback .feedback-item__btn .link-flash::after {
  position: absolute;
  content: "";
  left: 0;
  bottom: 5px;
  display: block;
  width: 100%;
  height: 1px;
  background: #2b2b2b;
  transition: 1.1s cubic-bezier(0.19, 1, 0.22, 1);
}
#feedback .feedback-item__btn .link-flash::before {
  transform: scaleX(0);
  transform-origin: left;
}
#feedback .feedback-item__btn .link-flash::after {
  transform-origin: right;
  transition-delay: 0.25s;
}
#feedback .feedback-item__btn .link-flash:hover::before {
  transform: scaleX(1);
  transition-delay: 0.25s;
}
#feedback .feedback-item__btn .link-flash:hover::after {
  transform: scaleX(0);
  transition-delay: 0s;
}
#feedback .feedback-item__img img {
  display: block;
  width: 100%;
}
#feedback .feedback-button button {
  margin: 0 auto;
}

#feedback-modal {
  max-width: 615px;
  width: 100%;
  padding: 30px 25px 20px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 125%;
  color: #323232;
  border-radius: 20px;
  font-size: calc(0.00625 * 100vw + 12px);
}
@media (max-width: 768px) {
  #feedback-modal {
    padding: 30px 10px 10px;
    font-size: 16px;
    border-radius: 16px;
  }
}
@media (min-width: 1921px) {
  #feedback-modal {
    font-size: 24px;
  }
}
@media (max-width: 320px) {
  #feedback-modal {
    font-size: 14px;
  }
}