
@import "../../scss/_global.scss";

@mixin paddingContent() {
  padding: 0 30px 50px;

  @media (max-width: 1280px) {
    padding: 0 30px 40px;
  }

  @media (max-width: 1024px) {
    padding: 0 20px 30px;
  }

  @media (max-width: 768px) {
    padding-bottom: 95px;
  }
}

#feedback {
  overflow: hidden;
  @extend %indent-p;

  .feedback-wrapper {
    padding: 0 10px;
    @media (min-width: 769px) {
      padding: 0 20px;
      margin: 10px 50px;
    }
    &__container {
      position: relative;
    }

    .swiper-wrapper {
      padding-bottom: 50px;

      @media (max-width: 992px) {
        padding-bottom: 40px;
      }

      .swiper-slide {
        opacity: 0;
        transition: opacity 0.1s 0s;

        &-active {
          opacity: 1;
        }

        @media (min-width: 993px) {
          &-next + .swiper-slide {
            opacity: 1;
          }
        }

        @media (min-width: 769px) {
          &-next {
            opacity: 1;
          }
        }
      }
    }

    .feedback-navigation {
      @media (max-width: 768px) {
        position: absolute;
        display: flex;
        justify-content: space-between;
        top: auto;
        bottom: 20px;
        transform: translateY(calc(-60px)) translateX(-50%);
        left: 50%;
        width: 130px;
      }

      .feedback-btn__prev,
      .feedback-btn__next {
        position: absolute;
        z-index: 5;
        top: 50%;
        transform: translateY(calc(-50% - 60px));

        @media (max-width: 768px) {
          position: static;
          transform: translateY(0);
          left: auto;
          right: auto;
        }
      }

      .feedback-btn__prev {
        left: 0px;
      }

      .feedback-btn__next {
        right: 0px;
      }
    }
  }

  .feedback-item {
    overflow: hidden;
    @extend %shadow-block;
    // box-shadow: 5px 15px 20px rgba(0, 0, 0, 0.1);
    height: auto;
    display: flex;
    flex-direction: column;
    gap: 30px;

    @media (max-width: 992px) {
      // box-shadow: 5px 15px 15px rgb(0 0 0 / 10%);
      gap: 20px;
    }

    @media (max-width: 768px) {
      max-width: 300px;
      margin: 0 auto;
    }

    &__line {
      @include paddingContent();
      padding-bottom: 0 !important;
    }

    &__btn {
      @include paddingContent();
      display: flex;
      justify-content: center;

      .link-flash {
        font-family: "Roboto";
        font-style: normal;
        font-weight: 400;
        font-size: 22px;
        line-height: 115%;
        white-space: nowrap;
        text-align: center;

        @media (max-width: 768px) {
          font-size: 16px;
        }
        @include fluidFontSize(14, 22, 320, 1920);
        @include linkHover($gray);
      }
    }

    &__img {
      img {
        display: block;
        width: 100%;
      }
    }
  }

  .feedback-button {
    button {
      @extend %button;
      margin: 0 auto;
    }
  }
}

#feedback-modal {
  max-width: 615px;
  width: 100%;
  padding: 30px 25px 20px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 125%;
  color: #323232;
  border-radius: 20px;

  @media (max-width: 768px) {
    padding: 30px 10px 10px;
    font-size: 16px;
    border-radius: 16px;
  }

  @include fluidFontSize(14, 24, 320, 1920);
}
